import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
  {
    path: '/',
    name: 'test',
    meta: {
        title: '支付', 
    },
    component: () => import('./views/pay/test.vue')
  },
  ]
})
