<template>
  <div id="app">
    <div id="nav" >
      <el-container style="height:100%;">
        <el-aside style="width:auto;" v-show="this.$route.meta.show"  >
          <div style="height:100%;" 
          >
          <el-menu
            default-active="1"
            :router="true"
            class="el-menu-vertical"
            :collapse="isCollapse"
            style="height:100%;"
          >
            <div class="logo" @click="shownav"><img :src="logo" alt="天才乔治" title="天才乔治" srcset=""></div>
            
            <el-submenu index="7" v-if="level==1">
              <template slot="title">
                <i class="iconfont icon-nav_user"></i>
                <span>后台管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="agencyuser">代理管理</el-menu-item>
                <el-menu-item index="merchantuser">商户管理</el-menu-item>
                <el-menu-item index="orders">订单管理</el-menu-item>
                <el-menu-item index="makecasemanage">提现管理</el-menu-item>
                  <el-menu-item index="bankmanage">银行管理</el-menu-item>
                  <el-menu-item index="devices">设备管理</el-menu-item>
                  <el-menu-item index="announcement">公告管理</el-menu-item>
                <el-menu-item index="operatingdata">操作记录</el-menu-item>
                <el-menu-item index="dataclear">数据清理</el-menu-item>
              </el-menu-item-group>
            </el-submenu>

            <el-submenu v-if="level==3"  index="1">
              <template slot="title">
                <i class="iconfont icon-yonghu1"></i>
                <span>用户信息</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="basicinfo">基本设置</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="2" v-if="level==3">
              <template slot="title">
                <i class="iconfont icon-icon_category"></i>
                <span>订单管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="orderlist">订单查询</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu index="3" v-if="level==3">
              <template slot="title">
                <i class="iconfont icon-weixinzhifu"></i>
                <span>结算管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="withdraw">提现查询</el-menu-item>
                <el-menu-item index="takecash">申请提现</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu v-if="level==2"  index="4">
              <template slot="title">
                <i class="iconfont icon-nav_user"></i>
                <span>代理管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="proxyuser">用户管理</el-menu-item>
                <el-menu-item index="makecasemanage">提现管理</el-menu-item>
                <el-menu-item index="proxyorderlist">订单管理</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu v-if="level==2"  index="5">
              <template slot="title">
                <i class="iconfont icon-icon1"></i>
                <span>银行管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="addbank">添加银行卡</el-menu-item>
                <el-menu-item index="bankmanage">管理银行卡</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-submenu v-if="level==2"  index="6">
              <template slot="title">
                <i class="iconfont icon-shouji
            "></i>
                <span>设备管理</span>
              </template>
              <el-menu-item-group>
                <el-menu-item index="devices">添加设备</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
            <el-menu-item v-if="level==2" index="operatingdata">操作记录</el-menu-item>
            
          </el-menu>
          </div>
        </el-aside>

        <el-container>
          <el-header style="padding:0;min-width:780px;" v-show="this.$route.meta.show">
            <el-menu
              :default-active="activeIndex"
              class="el-menu-demo"
              mode="horizontal"
              :router="true"
            >
              <el-menu-item index="/">数据总览</el-menu-item>
              <el-menu-item index="#"><a href="https://cfcfpay.github.io/docs/#/" target="_blank">开放文档</a></el-menu-item>
              <el-submenu style="float:right;" index="5">
                <template slot="title">{{username}}</template>
                <el-menu-item index="changepw">修改密码</el-menu-item>
				<el-menu-item index="changecpw">修改操作密码</el-menu-item>
                <el-menu-item index="#" @click="quit">退出登陆</el-menu-item>
              </el-submenu>
            </el-menu>
          </el-header>

          <el-main style="padding:0;height:100%; box-sizing: border-box;" >
            <transition name="el-fade-in-linear">
              <router-view/>
            </transition>
          </el-main>
          <el-footer v-show="this.$route.meta.show">Copyright @ 2019 超付</el-footer>
        </el-container>
      </el-container>
    </div>
  </div>
</template>



<style>
html,
body {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  overflow-y: hidden;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
  box-sizing: border-box;
}
#nav {
  margin: 0;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
}
.logo{
  width: 36px;
  height: 36px;
  margin: 0 auto;
  box-sizing: border-box;
  margin-top: 20px;
  margin-bottom: 10px;
  cursor: pointer;
}
.logo img{
  width: 100%;
  height: 100%;
}
.iconfont {
  vertical-align: middle;
  margin-right: 5px;
  margin-left: 3px;
  width: 24px;
  font-size: 18px;
  text-align: center;
}
.el-menu-vertical:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
  transform: translateZ(0)
}
element.style {
  width: auto;
}
.el-footer {
  font-size: 14px;
  color: #999999;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}
.el-menu-item a{
  text-decoration: none;
}
</style>

<script>
import logo from './assets/images/logo.png'
export default {
  data() {
    return {
      logo:logo,
      activeIndex: "1",
      isCollapse: false,
      username: sessionStorage.getItem("username"),
      level:sessionStorage.getItem('level')
    };
  },
  created(){
    this.level = sessionStorage.getItem('level')
  },
  watch: {
    /* eslint-disable no-unused-vars */
    $route: function(to, from) {
      if (
        this.$route.fullPath.indexOf("login") != -1 &&
        sessionStorage.getItem("token")
      ) {
        this.$router.replace({
          path: "/"
        });
      }else{
        this.level = sessionStorage.getItem('level')
        this.username = sessionStorage.getItem('username')
      }
    }
  },
  methods: {
    shownav() {
      this.isCollapse = !this.isCollapse;
    },
    showmenu(){
      this.isCollapse = false
    },
    hidemenu(){
      this.isCollapse = true
    },
    quit() {
      sessionStorage.clear();
      this.$router.replace({
        path: "login"
      });
    }
  }
};
</script>


